.footer {
  background-color: $footer-bg-color;
  text-align: center;

  @media screen and (max-width: 767px) {
    padding-top: 28px;
    padding-bottom: 28px;
  }

  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
    align-items: center;

    padding-top: 30px;
    padding-bottom: 30px;
  }
}

.copyright {
  display: block;

  @media screen and (max-width: 767px) {
    margin-bottom: 12px;
  }

  @media screen and (min-width: 768px) {
    display: inline-block;
  }
}

.footer-text {
  @media screen and (max-width: 767px) {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  color: $footer-text-color;
  font-size: 14px;
  line-height: 1.14;

  @media screen and (min-width: 768px) {
    display: flex;
    align-items: center;

    font-size: 16px;
    line-height: 1.187;
  }
}

.footer-link:hover,
.footer:focus {
  color: $accent-color;
  transition: $transition-timing $timing-function;
}


.footer-svg {
  margin-right: 10px;
  margin-left: 10px;
}

.team-modal {
  border: none;
  background: initial;
  cursor: pointer;

  text-decoration: underline;
  color: $footer-text-color;
}


.basicLightbox {
  
  overflow: scroll;
  display: flex;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0; top: 0; right: 0; bottom: 0;
  justify-content: center;
  align-items: center;

  animation: fadeIn ease 0.9s;

  @keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
  }
}

.team-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-top: 50px;

  @media screen and (max-width: (768px - 1px)) {
    margin-top: 70px;
  }

  width: 240px;

  background-color: $primary-title-color;
  border-radius: 15px;

  @media screen and (min-width: 1260px) {
    width: 800px;

    border-radius: 25px;
  }

  @media screen and (min-width: 768px) {
    margin-top: 45px;

    width: 800px;
  }
}

.team-card {

  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;

  margin: 20px;

  width: calc((100% - 80px) / 2);

  @media screen and (min-width: 768px) {
    width: calc((100% - 200px) / 5);
  }

  @media screen and (min-width: 768px) {
    margin: 30px;

    width: calc((100% - 300px) / 5); 
  }
}

.team-image {
  margin-bottom: 10px;

  width: 100%;
  height: auto;
  border-radius: 10%;
  box-shadow: 5px 5px 11px -2px $footer-text-color;

  transition: 1s;

  &:hover,
  &:focus{
      transform: scale(1.2); 
}
}

.team-name {
  margin-bottom: 5px;
  text-align: center;
  font-family: $title-color;
  font-weight: 700;
}

.team-role {
  font-family: '@font-face';
  
  color:  #000000;
  text-align: center;
}

.logo__icon-footer{
  margin-right: 10px;
  margin-top: 10px;

}


.team-modal {
  text-decoration: underline;

  cursor: pointer;
}

.team-linkd:hover,
.team-linkd:focus {
      transform: scale(1.2); 
}

