.swiper-backdrop {
  opacity: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .66);
  transition: opacity .35s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
}

.swiper-link > * {
  pointer-events: none;
}

.swiper-link > img {
  border-radius: 5px;
  transition: transform .35s cubic-bezier(.4, 0, .2, 1);
}

.swiper-link:hover > img, .swiper-link:focus > img {
  transform: scale(1.2);
}

.swiper-link:hover .swiper-backdrop, .swiper-link:focus .swiper-backdrop, .swiper-link:hover .swiper-backdrop > .icon-play, .swiper-link:focus .swiper-backdrop > .icon-play {
  opacity: 1;
}

.swiper, swiper-container {
  z-index: 1;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: block;
  position: relative;
  overflow: hidden;
}

.swiper-wrapper {
  width: 100%;
  height: 100%;
  z-index: 1;
  transition-property: transform;
  transition-timing-function: var(--swiper-wrapper-transition-timing-function, initial);
  box-sizing: content-box;
  display: flex;
  position: relative;
}

.swiper-slide, swiper-slide {
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  transition-property: transform;
  display: block;
  position: relative;
}

.swiper-button-prev, .swiper-button-next {
  z-index: 10;
