$timing-function: cubic-bezier(0.4, 0, 0.2, 1);
$animation-time: 500ms;

.is-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  margin: -1px;
}
.backdrop {
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  opacity: 1;

  transition: opacity $animation-time $timing-function;

  -webkit-transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 999;
}

.backdrop_is-hidden {
  opacity: 0;
  pointer-events: none;
  & .modal {
    transform: translate(-50%, -50%) scaleX(0.1) scaleY(0.1);
  }
}

.close-btn {
  position: absolute;
  top: 8px;
  right: 8px;
  display: flex;
  padding: 8px;
  cursor: pointer;
  border: none;
  background-color: transparent;
  stroke: #000000;

  &:hover,
  &:focus .btn-close__icon {
    fill: getColor('brand-color-first');
  }
}

.close-btn__icon {
  fill: getColor('font-colot-hed');
  transition: fill $animation-time $timing-function;
}

.modal {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 50%;
  left: 50%;
  // transform: translate(-50%, -50%) scale(1);
  // top: 50%;
  // left: 50%;
  transform: translate(-50%, -50%) scaleX(1) scaleY(1);
  width: calc(100vw - 10px);
  background-color: #ffffff;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  transition: scale 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (max-width: 479px) {
    max-width: 280px;
    padding: 48px 20px 40px 20px;
    max-height: calc(100vh - 20px);

    overflow-y: scroll;
  }
  @media screen and (min-width: 480px) {
    max-width: 460px;
    padding: 48px 20px 40px 20px;
  }
  @media screen and (min-width: 768px) {
    max-width: 704px;
    padding: 40px 36px 64px 36px;
  }
  @media screen and (min-width: 1280px) {
    max-width: 806px;
    padding: 40px 12px;
  }
}

.backdrop .is-hidden .modal {
  -webkit-transform: translate(-50%, -50%) scale(0.9);
  transform: translate(-50%, -50%) scale(0.9);
}

.modal__container {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}
.modal__img {
  display: block;
  border-radius: 5px;

  height: fit-content;
  @media screen and (max-width: 479px) {
    width: 240px;
    margin-bottom: 20px;
  }
  @media screen and (min-width: 768px) {
    width: 264px;
    object-fit: cover;
    @media screen and (max-width: 767px) {
      min-width: 240px;
      height: 357px;
      margin-bottom: 20px;
    }

    @media screen and (min-width: 768px) {
      width: 264px;
      height: 374px;
      margin-right: 68px;
    }
    @media screen and (min-width: 1280px) {
      width: 375px;
      height: 478px;
      margin-right: 16px;
    }
  }

  .modal__title {
    text-transform: uppercase;
    margin-bottom: 20px;

    @media screen and (max-width: 1279px) {
      font-size: 20px;
      line-height: 1.16;
    }
    @media screen and (min-width: 1280px) {
      font-size: 30px;
      line-height: 1.16;
    }
  }

  // .characteristics {
  //   display: flex;
  //   justify-content: space-between;
  // }

  .charact__list {
    display: inline-block;
    margin-bottom: 20px;

    &:first-child {
      @media screen and (max-width: 1279px) {
        margin-right: 40px;
      }
      @media screen and (min-width: 1280px) {
        margin-right: 85px;
      }
    }
  }
  .charact__name {
    color: #8c8c8c;
    font-size: 12px;
    line-height: 1.33;
    margin-bottom: 8px;
  }

  .charact__value {
    display: block;
    max-width: 200px;
    font-size: 12px;
    line-height: 1.33;
    margin-bottom: 8px;
  }
  .charact__value > span {
    display: inline-block;
    padding: 1px 4px;
    background-color: #f7f7f7;
    border-radius: 5px;
  }
  .charact__value > .acsent {
    background-color: #b92f2c;
    padding: 1px 10px;
    color: #ffffff;
  }
  .modal__about > p {
    font-size: 12px;
    line-height: 1.33;
    margin-bottom: 8px;
  }
  .modal__about > .about {
    font-size: 12px;
    line-height: 1.66;
    margin-bottom: 10px;
  }
  // .modal__button {
  //   font-size: 12px;
  //   line-height: 1.33;
  //   margin-bottom: 20px;
  // }
  .modal__button {
    font-size: 12px;
    line-height: 1.16;
    text-transform: uppercase;
    background-color: transparent;
    border-radius: 100px;
    border: 1px solid #000000;
    color: #b92f2c;
    transition: color $animation-time $timing-function;
    transition: background-color $animation-time $timing-function;

    &:hover,
    &:focus {
      border: 1px solid #b92f2c;
      background-color: #b92f2c;
      color: #ffffff;
      cursor: pointer;
    }

    @media screen and (max-width: 479px) {
      padding: 10px 12px;
      margin-right: 4px;
    }
    @media screen and (min-width: 768px) {
      padding: 12px 18px;
      margin-right: 8px;
    }
    @media screen and (min-width: 1280px) {
      padding: 14px 32px;
      margin-right: 8px;
    }
  }

  #player-container {
    margin-top: -180px;
    margin-left: auto;
    margin-right: 50px;
  }

